// zel bir ey deil, sadece CPU'yu ok kullanan bir hesaplama grevi
export function hesapla(sayi: number, ustu: number): number {
  let result = 0
  let i = 0
  const len = Math.pow(sayi, ustu)
  while (i < len) {
    result += Math.sin(i) * Math.sin(i) + Math.cos(i) * Math.cos(i)
    i++
  }

  return result
}
